@import "./../base/vars";
.answers {
  &__title {
    margin-top: 70px;
    font-family: Lato Bold;
    font-size: 72px;
    color: $accentColor;
    text-align: center;

    @media (max-width: 992px) {
      margin-top: 40px;
    }
  }
  &__text--top {
    font-family: Lato-Italic;
    font-size: 20px;
    text-align: center;
    margin: 20px 0 50px;
    @media (max-width: 900px) {
      margin: 18px 0 0;
    }
  }
  &__subtitle {
    font-family: Lato Bold;
    font-size: 23px;
    margin-top: 36px;
    position: relative;
    bottom: 12px;
  }
  &__text {
    font-family: Lato-Regular;
    font-size: 16px;
    color: #8d8f94;
    margin-top: 36px;
    line-height: 160%;
  }
  &__btn {
    width: 238px;
    height: 72px;
    background-color: #02a0e3;
    color: #ffffff;
    border-radius: 5px;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    -ms-border-radius: 5px;
    -o-border-radius: 5px;
    font-family: Lato-Medium;
    font-size: 24px;
    box-shadow: 0px 4px 18px rgba(52, 119, 196, 0.35),
      inset 0px 1px 0px rgba(255, 255, 255, 0.3);
    margin-top: 48px;
  }
  &__btn:hover {
    background-color: #5eb0f3;
  }
  &__button {
    display: flex;
    justify-content: center;
  }
}
details > summary {
  list-style-type: none;
  background-color: #f6f7fb;
  margin-top: 10px;
  padding: 0 10px;
}

details > summary::-webkit-details-marker {
  display: none;
}
summary {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
}
details[open] {
  background-color: #f6f7fb;
  // img {
  //   transform: rotate(45deg);
  //   -webkit-transform: rotate(45deg);
  //   -moz-transform: rotate(45deg);
  //   -ms-transform: rotate(45deg);
  //   -o-transform: rotate(45deg);
  // }
}
.title {
  @media all and (max-width: 1300px) {
    font-size: 3.4rem;
  }
  @media all and (max-width: 1100px) {
    font-size: 3.2rem;
  }
  @media all and (max-width: 900px) {
    font-size: 2.5rem;
  }
  @media all and (max-width: 900px) {
    font-size: 2rem;
  }
}

.plus-minus,
.plus-minus2,
.plus-minus3,
.plus-minus_new {
  // display: none;
  right: 0px;
  top: 0px;
  background: url(./../images/dekor/Plus.png) no-repeat center;
  transition: 0.2s;
  position: relative;
  top: 10px;
  width: 15px;
  height: 15px;
  background-size: cover;
  z-index: 2;
}
.plus-minus_active,
.plus-minus3_active,
.plus-minus2_active {
  background: url(./../images/dekor/minus.png) no-repeat center;
  background-size: contain;
  position: relative;
  top: 10px;
  width: 15px;
  height: 15px;

  // background-size: cover;
  z-index: 2;
}
.plus-minus_new {
  right: 0px;
  top: 0px;
  background: url(./../images/dekor/Plus.png) no-repeat center;
  transition: 0.2s;
  position: relative;
  top: 10px;
  width: 15px;
  height: 15px;
  background-size: cover;
  z-index: 2;
}

.plus {
  width: 15px;
  height: 15px;
}
